<template>
  <div>
    <h1 class="title">Search Page</h1>
    <b-input-group prepend="Search Query:" id="search-input">
      <b-form-input v-model="searchQuery"></b-form-input>
      
      <b-form-input v-if="positionFlag===true" v-model="position" placeholder="Position Number">
        <b-input-group-append>
          <b-button @click="simpleSearchPlayer()" variant="success">Search</b-button>
        </b-input-group-append>
      </b-form-input>

      
      <b-input-group-append>
        <b-button @click="startSearch()" variant="success">Search</b-button>
      </b-input-group-append>

      <!-- <b-form-input v-else-if="searchTeamFlag===true">
        <b-input-group-append>
          <b-button @click="searchTeam()" variant="success">Search</b-button>
        </b-input-group-append>
      </b-form-input>

      <b-form-input v-else>
        <b-input-group-append>
          <b-button @click="simpleSearchPlayer()" variant="success">Search</b-button>
        </b-input-group-append>
      </b-form-input> -->


    </b-input-group>

    <br/>
    Your search Query: {{searchQuery }}
    <br>
      <input  v-if="!teamsearch" type="checkbox" id="postion" value="position" v-model="checkedNames">
      <label for="jack">Filter By position</label>
            
     <input  v-if="!teamsearch" type="checkbox" id="player" value="player" v-model="checkedNames">
     <label v-if="!teamsearch" for="jack">Search player__</label>
        
      <input v-if="!playersearch" type="checkbox" id="team" value="team" v-model="checkedNames">
      <label  for="john">Search Team</label>
      
  <br>

<span>Checked names: {{checkedNames}}</span>
<span v-for="res in searchResult" :key="res">
      <PlayerPreview :propObj="res"></PlayerPreview>
</span>

  <TeamPreview :propObj="results"></TeamPreview>



  <!-- <h2>Search Results</h2>
    <span v-if( status != 0 ) v-for="res in result.data" :key="res.position">
      <pokemon-preview :position="res.position"></pokemon-preview>
      <PlayerPreview></PlayerPreview> 
   </span> -->

  </div>

  
</template>

<script>

 import  PlayerPreview from "../components/PlayerPreview";
export default {
  components: { PlayerPreview },
  //   props:{
  //   anObject: Object
  // },
  data() {
    return {
      searchQuery:"",
      checkedNames:[],
      positionFlag: false,
      playersearch: false,
      teamFlag: false,
      teamsearch: false,
      searchTeamFlag:false,
      position:0,
      teamname:"",
      results: Object,
      status:0
    };
  },
  methods:{

  async simpleSearchPlayer(results){
        try {
          let check= [this.searchQuery,this.position,this.teamname]
          console.log(check);
          const params = {
               name:  this.searchQuery,
            };
          results = await this.axios.get(
          `http://localhost:3000/guest/Search/player/${params.name}`
          );
      } catch (err) {
        console.log("server:"+err.response);
      }
      this.status=results.status
      console.log(this.status);
      console.log(results);
      this.results= results.data
    },

  async filterByPosition(results){
       try {
            let check= [this.searchQuery,this.position,this.teamname]
            console.log(check);
            const params = {
            name:  this.searchQuery,
            position: this.position,
            teamname: this.teamname
            };
            results = await this.axios.get(
          `http://localhost:3000/guest/Search/filyer/filter/${params.name,params.position,params.teamname}`
          );
      } catch (err) {
        console.log("server:"+err.response);
       
        console.log(results);
      }
    },

    async searchTeam(results){
        try {
          let check= [this.searchQuery]
          console.log(check);
          const params = {
               name:  this.searchQuery,
            };
          results = await this.axios.get(
          `http://localhost:3000/guest/Search/team/${params.name}`
          );
      } catch (err) {
        console.log("server:"+err.response);
      }
      this.status=results.status
      console.log(this.status);
      console.log(results);
      this.results= results.data
    },


    async startSearch(){
       if(playersearch){
          if(!positionFlag && !teamFlag){
            await this.simpleSearchPlayer();
          }
          else{ await this.filterByPosition();}
        }
        else if(searchTeam){
          await this.searchTeam();
        }
       }
    },

  computed:{
      searchResult(){
        if(this.status===200){
          return this.results
        }
      else return [];
  }},
  updated() {
    if (this.checkedNames.find( element => element === "position")){
        this.positionFlag=true;
      }
    else this.positionFlag=false;

    if( this.checkedNames.find( element => element === "player")){
        this.playersearch=true;
        this.positionFlag=false;
        this.teamsearch=false;
      }
    else this.playersearch=false;

    if( this.checkedNames.find( element => element === "team")){
            this.playersearch=false;
            this.positionFlag=false;
            this.teamsearch=true;
          }
    else this.teamsearch=false;
}
}
</script>

<style scoped>

#search-input {
  margin-left: 20px; 
  width: 500px; 
}
</style>